body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  
}
.App {
  text-align: center;
  font-family: 'Poppins', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.navbar-light .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(100,252,113, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E") !important;
  }
  .nav{
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    z-index: 1;        
    transition-timing-function: ease-in;
    transition: all 0.5s;
    
}

.nav_black{
    background-color: rgba(8, 14, 40, 0.8); 
    /* background-color:  rgba(8, 14, 40); */
    z-index: 1000;
}
.navbar-light .navbar-nav .nav-link{
    color: white !important;
}

  .navbar-light .navbar-nav .nav-link:hover{
      color: #64fc71 !important;
      transition: 0.3s; 
  }

.wrapper{
    display: inline-flex;
    padding: 10px; 
   
  }
  .wrapper .icon{      
    margin: 0 20px;
    text-align: center;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    z-index: 2;
    transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
  .wrapper .icon span{
    color: #333 !important;
    display: block;
    height: 30px;
    width: 30px;
    background: #fff;
    border-radius: 50%;
    position: relative;
    z-index: 2;
    box-shadow: 0px 10px 10px rgba(0,0,0,0.1);
    transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
  .wrapper .icon span i{
    line-height: 30px;
    font-size: 15px;
  }
  

  .wrapper .icon:hover span{
    color: #fff !important;
  }
  
  
  .wrapper .facebook:hover span
  {
    background: #3B5999;
  }
  .wrapper .linkedin:hover span
  {
    background: #0e76a8;
  }
  .wrapper .twitter:hover span
  {
    background: #46C1F6;
  }
  .wrapper .instagram:hover span{
    background: #e1306c;
  }
  .wrapper .github:hover span{
    background: #333;
  }
  @media all and (max-width:500px){
    .wrapper .icon{
        margin: 0 10px;
        text-align: center;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        position: relative;
        z-index: 2;
        transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
      }
    .wrapper .icon span{
        display: block;
        height: 40px;
        width: 40px;
        background: #fff;
        border-radius: 50%;
        position: relative;
        z-index: 2;
        box-shadow: 0px 10px 10px rgba(0,0,0,0.1);
        transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
      }
      .wrapper .icon span i{
        line-height: 40px;
        font-size: 20px;
      }
  } 
  
.home-header{
    
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0 15px;
   
}

.home-header h4{
    color: white;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}
.home-header-designation {
    width: 90%;
background-color: #64fc71;
padding: 8px;
border-radius: 5px;
align-self:center;

}
.home-content{
    
    background: url(/portfolio/static/media/bg.b3ad0bbb.jpg)top right;  
    width: 100%;
    height: 100vh;
    position: relative;
    background-size: cover;    
    background-repeat: no-repeat;
    margin: 8px 5px;
}
.home-content:before {
    content: "";
    background: rgba(0, 0, 0, 0.65);
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
  }
  

@keyframes up-down{
    0% {
        transform: translateY(5px );
    }
    100% {
        transform: translateY(-5px);
    }
}

.down-arrow svg{
    transition: 0.4s;    
    animation: up-down 1s ease-in-out infinite alternate-reverse both;
    
}
@media (max-width: 500px) {
    .home-content {
      background-attachment: fixed;
      /* background: url(../Images/bgm.jpg)top right ;   */
    }
    
  }
  .global-class-name{
      margin: 5px;
  }
  .home-flex{
      display: flex;
      flex-direction: column;
  }
  .home{
    background-color: #000414; 
    height: 100vh;
    width: 100%;
  }

.about h2{
    
    font-size: 32px;
    font-weight: 700;
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    margin-bottom: 20px;
    padding-bottom: 0;
    /* color: #3b434a; */
    color:#000414 ;
    position: relative;
    z-index: 2;
}
.about{
    padding: 30px;
}
.card {
    margin: 0 auto;
    padding: 10px 10px;
    box-shadow: 4px 4px 8px 4px rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 90%;    
    border-radius: 5px;
  }
  
  .card:hover {
    box-shadow: 8px 8px 16px 8px rgba(0,0,0,0.2);
  }
  .row {
    --bs-gutter-x: 1.5rem;
    --bs-gutter-y: 0;
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(-1 * var(--bs-gutter-y));
    margin-right: calc(-.5 * var(--bs-gutter-x));
    margin-left: calc(-.5 * var(--bs-gutter-x));
  }
  .image {
    background: url(/portfolio/static/media/me.d0e09f3e.jpg) center center no-repeat;
    background-size: contain;
    min-height: 200px;
    padding: 10px;
    
  }
  .progress {
    height: 60px;
    display: block;
    background: none;
    border-radius: 0;
    overflow: hidden;
    font-size: .75rem;
  }
  .skill {
    padding: 10px 0;
    margin: 0 0 6px 0;
    text-transform: uppercase;
    display: block;
    font-weight: 700;
    font-family: "Raleway", sans-serif;
    color: #3b434a;
}
.val {
    float: right;
    font-style: normal;
}
.progress-bar-wrap {
    background: #e6e8eb;
}
.progress-bar {
    width: 1px;
    height: 10px;
    transition: 0.9s;
    background-color: #64fc71;
}
.progress-bar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    color: #fff;
    text-align: center;
    white-space: nowrap;
}
.about .content ul {
    list-style: none;
    padding: 0;
}
.content ul i {
    font-size: 16px;
    margin-right: 5px;
    color: #64fc71;
    line-height: 0;
}
 .details{
    display: flex;
    justify-content: space-evenly;
    text-align: left;
    text-align: initial;
    flex-direction: row;
    
} 
.skill-content{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.skill-one{
    width: 100%;
    padding: 10px;
}
.skill-two{
    width: 100%;
    padding: 10px;
}
@media (max-width: 540px) {
    .details{
        display: flex;
        justify-content: space-evenly;
        text-align: left;
        text-align: initial;
        flex-direction: column;
        
    } 
.content ul i {
    font-size: 10px;
    margin-right: 5px;
    color: #64fc71;
    line-height: 0;
}
.content ul span {
