@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;500&family=Roboto:wght@300&display=swap');
a{
    text-decoration: none;
    color: white;
}
a:hover{
    text-decoration: none;
    color: #64fc71;
}
.project{
    width: fit-content;
}
.project__top{
    font-family: 'Poppins', sans-serif;
    background-color: #000414;
    width: 470px;
    text-align: center;
    padding: 10px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.project__bottom img{
     width: 470px; 
    height: 400px;
    object-fit: cover;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;  
    transition: .5s ease;
    backface-visibility: hidden;
}

.text {
      
    background-color: rgba(0, 4, 20);    
    font-size: 16px;
    padding: 16px 32px;
    color: #64fc71; 
    margin-top: -5px;
  }
  
@media (hover){
    .middle {
        width: 470px;
        transition: .5s ease;
        opacity: 0; 
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        text-align: center;
        
        
      }  
     
      .project__bottom:hover .image {
        opacity: 0.3;
      }
      
      .project__bottom:hover .middle {
        opacity: 1;
       
      } 
      
}
@media all and (max-width:500px){
    .project__top{
        
        width: 200px;
        
    }
    
    .project__bottom img{
        max-width: 200px;
        height: 180px;
        object-fit: cover;
    }
    .middle{
        max-width: 200px;
        font-size: 12px;
    } 
}